import classes from './page.module.css'
function HomePage() {
  let today = new Date()
  let year = today.getFullYear()
  let month = today.getMonth() + 1
  let date = today.getDate()
  let h = today.getHours()

  return (
    <>
      <h1>      .</h1>
      <div className={classes.timer}>
        <p>
           {year} {month} {date}.
        </p>
        <p>
            {h}
          {h < 5 &&
            '   .      ?'}
          {h >= 5 &&
            h < 10 &&
            '   ?      .'}
          {h >= 10 && h < 19 && '   .'}
          {h >= 19 && h <= 23 && '  ?'}
        </p>
      </div>
    </>
  )
}

export default HomePage
