import { useState } from 'react'
import { Form, json, redirect, NavLink } from 'react-router-dom'
import classes from './Login.module.css'

function SignUp({ accounts }) {
  const [id, setId] = useState('')
  const [password, setPassword] = useState('')
  const [redId, setRedId] = useState(false)
  const [redPassword, setRedPassword] = useState(false)
  const [username, setUsername] = useState('')

  const usernameHandler = (e) => {
    setUsername(e.target.value)
  }

  const idHandler = (e) => {
    setId(e.target.value)
    if (id.trim().length < 4) {
      setRedId(true)
    } else {
      setRedId(false)
    }
  }

  const passwordHandler = (e) => {
    setPassword(e.target.value)
    if (password.trim().length < 7) {
      setRedPassword(true)
    } else {
      setRedPassword(false)
    }
  }

  return (
    <Form method="post" className={classes.form}>
      <div>
        <label htmlFor="name"> </label>
        <input
          id="username"
          type="text"
          name="username"
          className={classes.inputUp}
          value={username}
          onChange={usernameHandler}
          required
        ></input>
      </div>
      <div>
        <label htmlFor="id"> </label>
        <input
          id="id"
          type="text"
          name="id"
          value={id}
          className={classes.inputUp}
          onChange={idHandler}
          required
        ></input>
        {redId && <p> 4 </p>}
      </div>
      <div>
        <label htmlFor="password"> </label>
        <input
          id="password"
          type="password"
          name="password"
          className={classes.inputUp}
          onChange={passwordHandler}
          value={password}
          required
        ></input>
        {redPassword && <p> 8 </p>}
      </div>
      {!redId &&
        !redPassword &&
        username !== '' &&
        id !== '' &&
        password !== '' && <button></button>}
      <NavLink to="/login">
        <button></button>
      </NavLink>
    </Form>
  )
}

export default SignUp

export async function action({ request, params }) {
  const data = await request.formData()
  const method = request.method
  const eventData = {
    id: data.get('id'),
    password: data.get('password'),
    username: data.get('username'),
  }
  const accounts = await fetch(
    'https://shrub-terrific-beginner.glitch.me/login'
  )
  const account = await accounts.json()
  for (let i = 0; i < account.length; i++) {
    if (
      account[i].username === eventData.username ||
      account[i].id === eventData.id
    ) {
      window.alert('       ')
      return redirect('/login/register')
    }
  }

  const response = await fetch(
    'https://shrub-terrific-beginner.glitch.me/login',
    {
      method: method,
      headers: {
        'Content-Type': 'application/json;charset=utf-8',
      },
      body: JSON.stringify(eventData),
    }
  )

  if (!response.ok) {
    throw json({ message: 'Could not save event.' }, { status: 500 })
  }
  window.alert('   .')
  return redirect('/login')
}
